import React from 'react';

import { SpinerWrap, LoadingWrap } from './style';

type SpinnerProps = {
  size: Number
};

const Spinner = ({ size }: SpinnerProps) => {
  return (
    <SpinerWrap size={size}>
      <div className="inner" />
    </SpinerWrap>
  );
};

type LoadingProps = {
  size?: Number
};

const Loading = ({ size }: LoadingProps) => {
  return (
    <LoadingWrap>
      <Spinner size={size} />
    </LoadingWrap>
  );
};

Loading.defaultProps = {
  size: 50
};

export default Loading;
